.div-reportes {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
  margin-top: 2rem;
  text-align: center;
}
.div-reportes .caja-riego {
  background-color: #EC9B22;
  border-radius: 12px;
}
.div-reportes .caja-humedadSuelo {
  background-color: #03C74E;
  border-radius: 12px;
}
.div-reportes .caja-temperatura {
  background-color: #23B8FA;
  border-radius: 12px;
}
.div-reportes .caja-humedadRelativa {
  background-color: #0FB7EF;
  border-radius: 12px;
}
.div-reportes .icon {
  width: 150px;
  height: auto;
}
.div-reportes p {
  font-size: 2rem;
  font-weight: bold;
}

.elementos-caja-superior {
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}

.elementos-caja-inferior {
  font-weight: bolder;
  color: black;
  font-size: 2.5rem;
  margin-top: 0;
}
.elementos-caja-inferior h4 {
  margin: 0;
}

.container-reportes {
  margin-bottom: 2rem;
}